// reset styles
* {
	padding: 0;
	margin: 0;
	border: 0;
}
*,
*:before,
*:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside {
	display: block;
}

html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}
button {
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}

//-----------------------

body {
	font-family: "Montserrat";
	background: #eaeaea;
}
.container {
	max-width: 1130px;
	margin: 0 auto;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	padding: 0 10px;
	overflow: hidden;
}
.wrapper {
	flex: 1 1 auto;
}
.slick-dots {
	bottom: -10px;
}
.slick-dots li button::before {
	content: "";
	background: #ffa542;
	box-shadow: 0px 0px 10px #ffa542;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	margin: 20px 0;
}
